.input{
  display: block;
  font-family: 'Satisfy', cursive;
    width: 19rem;
    height: 10rem;
    resize: none;
    font-size: larger;
    margin: 1% auto;
    border: 2px solid #222;
    border-radius: 15px;
    background-color: white;
    inline-size: 16rem;
    overflow-wrap: break-word;
}

.button{
  display: block;
    font-family: 'Satisfy', cursive;
    width: 200px;
    height: 4rem;
    resize: none;
    font-size: larger;
    margin: 20px auto;

    border: 2px solid green;
    border-radius: 5px;
    background-color: rgb(5, 179, 5);
    color: #fff;
    text-shadow: 1px 1px #444;
    font-size: 18px;
    cursor: pointer;
}